.weather {
  margin-top: 20px;
  border: 3px dashed grey;
  border-radius: 20px;
  padding-bottom: 30px;
  width: max-content;
  padding: 0px 20px 20px 20px;
  margin-left: 25%;
  background-color: rgb(115, 190, 190);
}
.weather .title {
  font-size: 3rem;
  justify-content: center;
  font-family: "Times New Roman", Times, serif;
}
.weather form {
  margin-top: 30px;
}
.weather form input {
  padding: 10px;
  border: none;
  border-radius: 2em;
  /* border-bottom: 2px dashed rgb(2, 57, 88); */
  margin-right: 10px;
  background-color: azure;
}

.weather form input:focus {
  outline: none;
}

.weather .getweather {
  margin-left: 10px;
  padding: 8px;
  font-size: 1.3rem;
  border: none;
  border-radius: 20px;
  border: 2px solid black;
  background: rgb(2, 57, 88, 0.8);
  cursor: pointer;
  color: whitesmoke;
}

.weather .getweather:hover {
  background: transparent;
  color: black;
}

.weather .getweather:focus {
  outline: none;
}

